import { useState } from "react";

const Add = ({ add }) => {
  const [fn, sfn] = useState("");
  const [ln, sln] = useState("");
  const [dob, sdob] = useState("");
  const [dsg, sdsg] = useState("");
  const [img, simg] = useState("");
  const [exp, sexp] = useState("");
  const hs = (e) => {
    e.preventDefault();
    add({ fn, ln, dob, dsg, img, exp });
    sfn("");
    sln("");
    sdob("");
    sdsg("");
    simg("");
    sexp("");
  };
  return (
    <form
      style={{
        display: "flex",
        justifyContent: "center"
      }}
      onSubmit={(e) => hs(e)}
    >
      <div>
        <span> Fitst name</span>
        <span>
          {" "}
          <input type="text" value={fn} onChange={(e) => sfn(e.target.value)} />
        </span>
      </div>
      <div>
        <span> Last name</span>
        <span>
          {" "}
          <input type="text" value={ln} onChange={(e) => sln(e.target.value)} />
        </span>
      </div>
      <div>
        <span> DOB</span>
        <span>
          {" "}
          <input
            type="text"
            value={dob}
            onChange={(e) => sdob(e.target.value)}
          />
        </span>
      </div>
      <div>
        <span> Designation</span>
        <span>
          {" "}
          <input
            type="text"
            value={dsg}
            onChange={(e) => sdsg(e.target.value)}
          />
        </span>
      </div>
      <div>
        <span> Img</span>
        <span>
          {" "}
          <input
            type="text"
            value={img}
            onChange={(e) => simg(e.target.value)}
          />
        </span>
      </div>
      <div>
        <span> Exp</span>
        <span>
          {" "}
          <input
            type="text"
            value={exp}
            onChange={(e) => sexp(e.target.value)}
          />
        </span>
      </div>
      <button type="submit">Add</button>
    </form>
  );
};
export default Add;
