import { useState } from "react";
import Add from "../Add/Add";
import Display from "../Display/Display";
import Edit from "../Edit/Edit";
import { Card } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
const Home = () => {
  const [d, sd] = useState([
    {
      fn: "first Name",
      ln: "Last name",
      dob: "1-2-1990",
      dsg: "deg",
      img: "",
      exp: "exp"
    }
  ]);
  const [f, sf] = useState(false);
  const [indd, sindd] = useState(0);
  const add = (v) => {
    sd([...d, v]);
  };
  const deletes = (ind) => {
    d.splice(ind, 1);
    sd(d);
  };
  const edit = (ind) => {
    sindd(ind);
    sf(true);
  };
  const update = (ind, v) => {
    d[ind]["fn"] = v.fn;
    d[ind]["ln"] = v.ln;
    d[ind]["dob"] = v.dob;
    d[ind]["dsg"] = v.dsg;
    d[ind]["img"] = v.img;
    d[ind]["exp"] = v.exp;
    console.log(d);
    sd(d);
  };
  return (
    <div>
      {f ? <Edit ind={indd} update={update} d={d} /> : <Add add={add} />}
      {d &&
        d.map((i, ind) => (
          <Card>
            <Card.Body>
              <Display
                key={"it" + ind}
                d={i}
                ind={ind}
                deletes={deletes}
                edit={edit}
              />
            </Card.Body>
          </Card>
        ))}
    </div>
  );
};
export default Home;
