import { useState } from "react";

const Edit = ({ update, ind, d }) => {
  const [fn, sfn] = useState(d[ind].fn);
  const [ln, sln] = useState(d[ind].ln);
  const [dob, sdob] = useState(d[ind].dob);
  const [dsg, sdsg] = useState(d[ind].dsg);
  const [img, simg] = useState(d[ind].img);
  const [exp, sexp] = useState(d[ind].exp);
  const hs = (e) => {
    e.preventDefault();
    sfn(fn);
    sln(ln);
    sdob(dob);
    sdsg(dsg);
    simg(img);
    sexp(exp);
  };
  return (
    <form onSubmit={(e) => hs(e)}>
      <div>
        Fitst name
        <input type="text" value={fn} onChange={(e) => sfn(e.target.value)} />
      </div>
      <div>
        Last name
        <input type="text" value={ln} onChange={(e) => sln(e.target.value)} />
      </div>
      <div>
        DOB
        <input type="text" value={dob} onChange={(e) => sdob(e.target.value)} />
      </div>
      <div>
        Designation
        <input type="text" value={dsg} onChange={(e) => sdsg(e.target.value)} />
      </div>
      <div>
        Img
        <input type="text" value={img} onChange={(e) => simg(e.target.value)} />
      </div>
      <div>
        Exp
        <input type="text" value={exp} onChange={(e) => sexp(e.target.value)} />
      </div>
      <button
        type="submit"
        onClick={() => update(ind, { fn, ln, dob, dsg, img, exp })}
      >
        Update
      </button>
    </form>
  );
};
export default Edit;
